//HERE OUR ALL PAGES ANIMTION
// in page down to up
// exit page up to down  

export const pageAnimation = {
  hidden: {
    opacity: 0,
    y: 300,
  },

  show: {
    opacity: 1,
    y: 0,

    transition: {
      duration: 1,
        when:"beforeChildren",
        stragger:5,
    },
  },



  exit: {
    opacity: 0,
    
    transition: {
      duration: 0.5,
    },
  },
};


export const titleAni = {
    hidden :{y : 200},
    show:{
        y:0,
        transition:{
            duration : 0.75, ease : "easeOut"
        }
    }



}


export  const fade = {
    hidden : {opacity : 0},
    show:{
        opacity:1,
        transition: {ease : "easeOut" , duration : 0.75}
    }


} 


export const PhotoAni = {
    hidden:{scale:1.5, opacity: 0},
    show:{

        scale: 1,
        opacity: 1,
        transition: {
            duration: 1,
            ease: "easeOut",
            delay : 2
        }


    }


}

export const lineAni  = {
  hidden :{
    width: "0"
  },

  show : {
    width : "100%",
      transition:{
        duration : 1,
        
      }

  }


}

export const slider = {

  hidden:{ x : "-130%" , skew : "45deg" },

  show:{
    x: "100%",
    skew: "0deg",
    transition : {ease : "easeOut" , duration : 1}
  }

}



